.fishing-banner {
  width: 100%;
  height: 50rem;
}
.fishing-component-a {
  height: 50rem;
  position: relative;
  display: flex;
  .content {
    padding: 2rem;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    margin: 2rem;

    .content-1,
    .content-2,
    .content-3 {
      height: 35rem;
      padding: 0;
      position: relative;
      display: flex;
      border-radius: 1rem;

      .content-data {
        position: absolute;
        top: 3rem;
        text-align: justify;
        padding: 2rem;
        line-height: 1.8rem;
        z-index: 0;

        h3 {
          text-align: center;
        }
        p {
          margin-top: 4rem;
        }
      }
    }
    img {
      width: 100%;
      height: 35rem;
      opacity: 1;
      z-index: 1;
      transition: 1s ease;
      border-radius: 1rem;
    }
    img:hover {
      opacity: 0.1;
      transition: 1s ease;
    }
    .content-1:hover,
    .content-2:hover,
    .content-3:hover {
      box-shadow: 0px 2px 10px 6px rgb(105, 104, 104);
    }
  }
}
.fishing-component-b,
.fishing-component-c {
  height: 40rem;
  position: relative;
}

.fishing-component-b {
  background-color: rgb(250, 242, 231);
  display: flex;
  .content {
    align-items: center;
    margin: 2rem;
    h1 {
      font-weight: 600;
      font-variant-caps: all-petite-caps;
    }
    .content-1 {
      padding: 2rem;
    }
    .content-2 {
      img {
        width: 100%;
      }
    }
  }
}
.fishing-component-c {
  background-color: rgb(255, 255, 255);
  display: flex;
  .content {
    align-items: center;
    margin: 2rem;
    h1 {
      font-weight: 600;
      font-variant-caps: all-petite-caps;
    }
    .content-2 {
      padding: 2rem;
    }
    .content-1 {
      img {
        width: 100%;
      }
    }
  }
}
